import React from 'react'
import "./Card.css";
import PropTypes from "prop-types";

const Card = ({lang, img, fcolor, scolor}) => {
    return (
        <div className="card"
            style = {
                {
                    background : `linear-gradient(to left,${fcolor}, ${scolor})`

                }
            }
        >
           <img src={img}  alt="lang.svg" />
           <h3>{lang}</h3>
        </div>
    )
}

Card.propTypes = {
    lang : String,
    img : String,
    fcolor : String,
    scolor : String,
}

export default Card
